import React from "react";

const ShowMore = (props) => {
    const { data } = props.location.state || {};
    console.log(data)
    if (!data) props.history.push("/Sorathiya/service");
    return data? (
        <section className="container">
            <h3 className="text-center mb-4 text-primary service_header">
                Our Services
            </h3>
            <div className="row">
                <div className="col-12  my-4 col-md-6 d-flex align-items-start flex-column animated">
                    <img src={data.imgPath} alt="" className="img-fluid m-auto drop-img-shadow" />
                </div>
                <div className="col-12  my-4 col-md-6 d-flex flex-column justify-content-center">
                    <h3 className="text-underline pb-1">{data.title}</h3>
                    <ul className="my-3">
                        {
                            data.content.split(".").map(content => (
                                <React.Fragment key={content.substr(0, 4) + Math.random()}>
                                    {content && <li>{content}.</li>}
                                </React.Fragment>
                            ))
                        }
                    </ul>
                </div>
                <div className="col-12 my-4 d-flex flex-column align-items-center">
                        <h4 className="text-center my-3">This is an dummy project if you like this please Contact me on</h4>
                        <div>
                        <button className="btn btn-outline-primary my-3 mx-2"
                            onClick={()=>props.history.goBack()}>
                            Back
                        </button>
                        <a className="btn btn-outline-primary my-3"
                        target="_blank"
                           href="https://wa.me/+919574879008/?text=My%name%is%Sorathiya%Afroz.%Thanks%To%Contact%Me">
                            Contact Me
                        </a>
                        </div>
                </div>
            </div>
        </section>
    ):""
};

export default ShowMore;