import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import About from "../Pages/About";
import Home from "../Pages/Home";
import Contact from "../Pages/Contact";
import Service from "../Pages/Service";
import ShowMore from "../Pages/ShowMore";
import Navbar from "../Components/Navbar";
const Routes = () => {
  return (
    <>
      <Navbar />
      <Switch>
        <Route exact path="/Sorathiya" component={Home} />
        <Route path="/Sorathiya/about" component={About} />
        <Route path="/Sorathiya/contact" component={Contact} />
        <Route exact path="/Sorathiya/service" component={Service} />
        <Route path="/Sorathiya/service/:service_Name" component={ShowMore} />
        <Redirect to="/Sorathiya" />
      </Switch>
    </>
  );
};
export default Routes;
