import React from "react";
import Card from "../Components/Card";
import Img1 from "../assets/undraw_chat_bot_kli5.svg";
import Img2 from "../assets/undraw_in_sync_xwsa.svg";
import Img3 from "../assets/undraw_operating_system_4lr6.svg";
import Img4 from "../assets/undraw_server_down_s4lk.svg";
import Img5 from "../assets/undraw_posting_photo_v65l.svg";
import Img6 from "../assets/undraw_mobile_inbox_3h46.svg";

const Service = (props) => {

  const allServices = [
    {
      imgPath: Img1,
      title: "Chat Bot Automation",
      content: "We are provide chat-bot automation services at low cost with full functionality.",
      btnName: "Show Price"
    },
    {
      imgPath: Img2,
      title: "Responsive Websites",
      content: "We are create mobile friendly web via talented developers.",
      btnName: "Explore More"
    },
    {
      imgPath: Img3,
      title: "Web Themes",
      content: "We are create best and smooth theme for web and app.",
      btnName: "Explore More"
    },
    {
      imgPath: Img4,
      title: "Server Issues",
      content: "We are provide best and faster server with low cost and without any issues.",
      btnName: "Start Hosting"
    },
    {
      imgPath: Img5,
      title: "Social Life",
      content: "Fully secure social chating with friends.",
      btnName: "Join Now"
    },
    {
      imgPath: Img6,
      title: "Authantication",
      content: "Fully secure with two-factor authentication.",
      btnName: "Show Price"
    }
  ];

  const handleService = (data) => {
    //   console.log(data.title.split(" ").join("-"))
   props.history.push({
      pathname:"/Sorathiya/service/"+data.title.split(" ").join("-"),
      state:{
        data:{...data}
      }
    })
  }

  return (
    <>
      <section className="container">
        <h3 className="text-center mb-4 text-primary service_header">
          Our Services
        </h3>
        <div className="row">
          {
            allServices.map(service => (
              <div key={service.title+Math.random()} className="col-md-4 col-sm-6 col-12 mb-2">
                <Card
                  imgPath={service.imgPath}
                  title={service.title}
                  content={service.content}
                  btnName={service.btnName}
                  onClick={()=>handleService(service)}
                />
              </div>
            ))
          }
        </div>
      </section>
    </>
  );
};
export default Service;
